:root {
  --violet: hsl(263, 55%, 52%);
  --dark-gray: hsl(217, 19%, 35%);
  --dark-blue: hsl(219, 29%, 14%);
  --white: hsl(0, 0%, 100%);
  --light-gray: hsl(0, 0%, 81%);

  --font-weight-bold: 600;
  --font-weight-semi: 500;
}

/* 
  || Smaller Devices
*/

@media screen and (max-width: 968px) {
  .app {
    max-width: 300px;
    align-self: center;
  }
  
  .testimonial-grid {
    gap: 1rem;
  }
}

/* 
  || Larger Devices
*/

@media screen and (min-width: 968px) {
  .app {
    max-width: 1100px;
  }

  .testimonial-grid {
    grid-template-columns: repeat(4, 1fr);
    gap: 1.5rem;
  }

  .testimonial:nth-child(1) {
    grid-column: 1 / 3;
    grid-row: 1;
  }

  .testimonial:nth-child(2) {
    grid-column: 3;
    grid-row: 1;
  }

  .testimonial:nth-child(3) {
    grid-column: 1;
    grid-row: 2;
  }

  .testimonial:nth-child(4) {
    grid-column: 2 / 4;
    grid-row: 2;
  }

  .testimonial:nth-child(5) {
    grid-column: 4;
    grid-row: 1 / 3;
  }
}

.app {
  margin: 3rem 1rem;
}

.testimonial-grid {
  display: grid;
}

.testimonial {
  background-color: var(--dark-gray);
  color: var(--light-gray);
  padding: 1.5rem 2rem;
  border-radius: 7px;
  -webkit-box-shadow: 5px 5px 15px 0px rgba(0,0,0,0.20); 
  box-shadow: 5px 5px 15px 0px rgba(0,0,0,0.20);
}

.testimonial:nth-child(1) {
  background-image: url('../public/bg-pattern-quotation.svg');
  background-position: 90% 0;
  background-repeat: no-repeat;
  background-color: var(--violet);
}

.testimonial:nth-child(2) {
  background-color: var(--dark-gray);
}

.testimonial:nth-child(4) {
  background-color: var(--dark-blue);
}

.testimonial:nth-child(3),
.testimonial:nth-child(5) {
  background-color: var(--white);
  color: var(--dark-gray);
}

.testimonial:nth-child(3) > .testimonial__header,
.testimonial:nth-child(5) > .testimonial__header {
  color: var(--dark-gray);
}

.testimonial__user {
  display: flex;
  align-items: center;
  margin-bottom: 1rem;
}

.testimonial__img {
  border-radius: 50%;
  width: 30px;
  height: 30px;
  margin-right: 1rem;
}

.testimonial__profile {
  display: flex;
  flex-direction: column;
}

.testimonial__name {
  font-size: 0.8rem;
  opacity: 0.8;
}

.testimonial__title {
  font-size: 0.7rem;
  opacity: 0.5;
}

.testimonial__header {
  margin-bottom: 1rem;
  color: var(--white);
  opacity: 0.9;
}

.testimonial__body {
  line-height: 1.5;
  opacity: 0.7;
}

.inverted {
  color: var(--dark-gray);
  background-color: var(--white);
}

.inverted > .testimonial__header {
  color: var(--dark-gray);
}